import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  Col,
  FormGroup,
  Row,
  Form,
  Input,
  Table,
  Spinner,
} from 'reactstrap';
import PropTypes from 'prop-types';
import MaterialIcon from '@material/react-material-icon';
import useAxios from '../../../hooks/useAxios';

const BusinessField = ({ setActiveStep }) => {
  const [isOpenForm, setIsOpenForm] = useState();
  const [isSending, setIsSending] = useState(false);
  const [values, setValues] = useState();

  const api = useAxios();

  const handleChange = (e) => {
    setValues((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);
    await api
      .post('vapi/klbi/store', values)
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };

  return (
    <Row lg="12" className="justify-content-center">
      <Col lg="12">
        <Card className="rounded-4 w-100">
          <CardBody>
            <h4 className="fw-bold">Daftar Bidang Usaha</h4>
            <Table hover>
              <tbody>
                <tr>
                  <td style={{ textAlign: 'left' }}>NIB</td>
                  <td style={{ textAlign: 'left' }}>37284</td>
                  <td style={{ textAlign: 'left' }}>
                    Konstruksi Bangunan Prasarana Sumber Daya Air
                  </td>
                  <td style={{ textAlign: 'left' }}>Resiko Rendah</td>
                  <td>
                    <button type="button" style={{ border: 'none', background: 'none' }}>
                      <MaterialIcon icon="delete" className="text-danger" />
                    </button>
                  </td>
                </tr>
                <tr>
                  <td style={{ textAlign: 'left' }}>NIB</td>
                  <td style={{ textAlign: 'left' }}>32134</td>
                  <td style={{ textAlign: 'left' }}>
                    Perdagangan Besar Suku Cadang Dan Aksesoris Mobil
                  </td>
                  <td style={{ textAlign: 'left' }}>Resiko Rendah</td>
                  <td>
                    <button type="button" style={{ border: 'none', background: 'none' }}>
                      <MaterialIcon icon="delete" className="text-danger" />
                    </button>
                  </td>
                </tr>
              </tbody>
            </Table>
            <div className="d-flex justify-content-end mt-2">
              {isOpenForm ? (
                <Button
                  type="button"
                  color="dark"
                  size="sm"
                  outline
                  onClick={() => setIsOpenForm(false)}
                  content="center"
                >
                  Tutup
                </Button>
              ) : (
                <Button
                  type="button"
                  color="dark"
                  size="sm"
                  outline
                  onClick={() => setIsOpenForm(true)}
                >
                  Tambah
                </Button>
              )}
            </div>
            {isOpenForm && (
              <Form onSubmit={handleSubmit}>
                <Col className="bg-light-primary rounded-4 px-3 pt-3 mt-3">
                  <Row>
                    <Col sm="12" lg="3">
                      <FormGroup>
                        <Input
                          type="text"
                          id="jenisIzinUsaha"
                          name="jenisIzinUsaha"
                          placeholder="Jenis Izin Usaha"
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="2">
                      <FormGroup>
                        <Input
                          type="text"
                          id="kode"
                          name="kode"
                          placeholder="Kode"
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Input
                          type="text"
                          id="bidangUsaha"
                          name="bidangUsaha"
                          placeholder="Bidang Usaha"
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="2">
                      <Button type="submit" color="dark">
                        {isSending ? (
                          <div className="d-flex align-items-center gap-2">
                            <Spinner size="sm" />
                            Menyimpan..
                          </div>
                        ) : (
                          'Tambah'
                        )}
                      </Button>
                    </Col>
                  </Row>
                </Col>
              </Form>
            )}
            <Form>
              <FormGroup className="d-flex justify-content-end mt-4">
                <Button
                  type="button"
                  color="primary"
                  className="me-2"
                  outline
                  onClick={() => setActiveStep(4)}
                >
                  Sebelumnya
                </Button>
                <Button type="button" color="primary" className="me-2 d-flex gap-2">
                  <MaterialIcon icon="send" />
                  Kirim
                </Button>
              </FormGroup>
            </Form>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

BusinessField.propTypes = {
  setActiveStep: PropTypes.func,
};

export default BusinessField;
