import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Col, Form, FormGroup, Input, Label, Row, Spinner } from 'reactstrap';
import useAxios from '../../../../hooks/useAxios';
import getBase64 from '../../../../utils/generateFile';

const IzinLainnya = ({ setSuccessMsg, setErrMsg }) => {
  const [datas, setDatas] = useState();
  const [file, setFile] = useState();
  const [isLoading, setIsLoading] = useState(false);

  const api = useAxios();
  const handleDoc = (e) => {
    getBase64(e.target.files[0])
      .then((res) => setFile(res.split(',')[1]))
      .catch((err) => console.log(err));
  };

  const handleChange = (e) => {
    setDatas((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    datas.file = file;
    await api
      .post('vapi/izin/store', datas)
      .then((res) => {
        console.log(res);
        setSuccessMsg('Data Izin Usaha Lainnya berhasil disimpan.');
      })
      .catch((err) => {
        console.log(err);
        setErrMsg('Gagal menyimpan data. Silakan coba lagi.');
      });
    setIsLoading(false);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Row>
        <Col sm="12" lg="4">
          <FormGroup>
            <Label htmlFor="nomor">Izin Usaha Lainnya (Jika ada)</Label>
            <Input
              type="text"
              id="nomor"
              name="nomor"
              placeholder="Nomor"
              onChange={(e) => handleChange(e)}
            />
          </FormGroup>
        </Col>
        <Col sm="12" lg="4">
          <FormGroup>
            <Label htmlFor="nama_izin">&nbsp;</Label>
            <Input
              type="text"
              id="nama_izin"
              name="nama_izin"
              placeholder="Nama Izin"
              onChange={(e) => handleChange(e)}
              required
            />
          </FormGroup>
        </Col>
        <Col sm="12" lg="4">
          <FormGroup>
            <Label htmlFor="tgl_terbit">&nbsp;</Label>
            <Input
              type="date"
              id="tgl_terbit"
              name="tgl_terbit"
              placeholder="Tanggal Dikeluarkan"
              onChange={(e) => handleChange(e)}
              required
            />
          </FormGroup>
        </Col>
      </Row>
      <Row className="mb-4">
        <Col sm="12" lg="4">
          <FormGroup className="position-relative">
            <Input type="file" id="file" name="file" onChange={(e) => handleDoc(e)} required />
            <small className="text-muted position-absolute">Upload file (max. 10mb)</small>
          </FormGroup>
        </Col>
        <Col sm="12" lg="4">
          <Button type="submit" color="secondary">
            {isLoading ? (
              <div className="d-flex align-items-center gap-2">
                <Spinner size="sm" />
                Menyimpan..
              </div>
            ) : (
              'Simpan'
            )}
          </Button>
        </Col>
      </Row>
    </Form>
  );
};

IzinLainnya.propTypes = {
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default IzinLainnya;
