import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Card, CardBody, Col, FormGroup, Row, Alert } from 'reactstrap';
// import { Link } from 'react-router-dom';
import MaterialIcon from '@material/react-material-icon';
import AktaPendirian from './docforms/AktaPendirian';
import AktaPerubahan from './docforms/AktaPerubahan';
import IzinBerusaha from './docforms/IzinBerusaha';
import IzinLainnya from './docforms/IzinLainnya';
import Lainnya from './docforms/Lainnya';

const Document = ({ setActiveStep }) => {
  const [successMsg, setSuccessMsg] = useState();
  const [errMsg, setErrMsg] = useState();

  return (
    <>
      {successMsg && (
        <Alert
          className="rounded-4 d-flex justify-content-between text-success fw-bold"
          color="success"
        >
          {successMsg}
          <MaterialIcon icon="close" onClick={() => setSuccessMsg(!successMsg)} />
        </Alert>
      )}
      {errMsg && (
        <Alert
          className="rounded-4 d-flex justify-content-between text-danger fw-bold"
          color="danger"
        >
          {errMsg}
          <MaterialIcon icon="close" onClick={() => setErrMsg(!errMsg)} />
        </Alert>
      )}
      <Row lg="12" className="justify-content-center">
        <Col lg="12">
          <Card className="rounded-4 w-100">
            <CardBody>
              <h4 className="fw-bold">Dokumen Perusahaan</h4>
              <AktaPendirian {...{ setSuccessMsg, setErrMsg }} />
              <AktaPerubahan {...{ setSuccessMsg, setErrMsg }} />
              <IzinBerusaha {...{ setSuccessMsg, setErrMsg }} />
              <IzinLainnya {...{ setSuccessMsg, setErrMsg }} />
              <Lainnya {...{ setSuccessMsg, setErrMsg }} />
              <FormGroup className="d-flex justify-content-end mt-4">
                <Button
                  type="button"
                  color="primary"
                  className="me-2"
                  outline
                  onClick={() => setActiveStep(2)}
                >
                  Sebelumnya
                </Button>
                <Button
                  type="button"
                  color="primary"
                  className="me-2"
                  onClick={() => setActiveStep(4)}
                >
                  Selanjutnya
                </Button>
              </FormGroup>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  );
};

Document.propTypes = {
  setActiveStep: PropTypes.func,
};

export default Document;
